@import "vars";
@import "media";

*,
*:before,
*:after {
  box-sizing: border-box;
}

html, body {
  scroll-behavior: smooth;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  font-style: normal;
  color: $black;
  margin: 0;
  background-color: $bg-white;
  min-width: 375px;
}

video {
  border: 0;
  outline: none;
}

img {
  outline: none;
  max-width: 100%;
}

._bold {
  font-weight: 700;
}

.burger {
  position: relative;
  width: 24px;
  height: 24px;
  display: none;

  @include mobile {
    display: block;
  }

  &:after,
  &:before {
    content: "";
    transform-origin: center;
  }

  &__item,
  &:after,
  &:before {
    display: block;
    position: absolute;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: $black;
    transition: opacity .1s, transform .2s;
  }

  &__item {
    top: 0;
  }

  &:after {
    top: 50%;
    transform: translateY(-50%);
  }

  &:before {
    bottom: 0;
  }

  &.active {
    &:after {
      transform: translateY(-50%) rotate(45deg);
    }

    &:before {
      bottom: 41%;
      transform: translateY(-50%) rotate(-45deg);
    }

    .burger__item {
      opacity: 0;
    }
  }
}

.anchor {
  position: absolute;
  top: -74px;
  left: 0;
}
