@import "../components/../inc/vars";
@import "../components/../inc/media";

.nav {
  display: flex;
  align-items: center;
  gap: 18px;

  &__item {
    display: block;
    position: relative;
    font-size: 18px;
    letter-spacing: 0.5px;
    font-weight: 600;
    color: $black;
    text-decoration: none;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 0;
      height: 2px;
      background-color: $black;
      transition: width 0.3s ease, left 0.3s ease;
    }

    &:hover:before {
      width: 100%;
      left: 0;
    }

    &:before {
      width: 0;
      left: auto;
      right: 0;
    }

    &:hover:before {
      left: 0;
      right: auto;
      width: 100%;
    }

  }

  @include mobile {
    position: fixed;
    top: 70px;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $bg-white;
    flex-direction: column;
    align-items: flex-start;
    padding: 100px 32px 24px 32px;
    z-index: -1;
    opacity: 0;
    transition: opacity .2s, transform .2s;

    transform: translateX(-100%);

    &__item {
      width: 100%;
    }

    &.active {
      opacity: 1;
      z-index: 10;
      transform: translateX(0);
      transition: transform .3s, opacity .3s;
    }
  }
}
