@import "../components/../inc/vars";
@import "../components/../inc/media";

.donate-box {
  font-size: 32px;
  font-weight: 600;
  color: midnightblue;
  text-align: center;
  background: $bg-white;
  margin-top: 160px;
  padding: 32px 60px;

  &__link {
    display: inline-block;
    font-size: 24px;
    color: $white;
    letter-spacing: 3px;
    text-decoration: none;
    background-color: $black;
    border-radius: 10px;
    margin-top: 21px;
    padding: 12px 20px;
  }
}
